import { gsap } from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

export function animateBgColorOnScroll(gsap: gsap) {
  const colors = {
    turquoise400: '#005C65',
    yellow100: '#FFAD00',
    red100: '#FF0D00'
  }

  return function callback(container: HTMLElement) {
    const tl = gsap.timeline({
      scrollTrigger: {
        scrub: true,
        end: () => `+=${container.offsetHeight - 750}`
      }
    })

    tl.to(
      container,
      {
        duration: 1,
        backgroundColor: colors.turquoise400,
        ease: 'none'
      },
      0
    )
      .to(
        container,
        {
          duration: 1,
          backgroundColor: colors.yellow100,
          ease: 'none'
        },
        1
      )
      .to(
        container,
        {
          duration: 1,
          backgroundColor: colors.red100,
          ease: 'none'
        },
        2
      )
  }
}
